.patientlist .MuiBadge-badge{
    width: 18px;
    height: 18px !important;
    border-radius: 50% !important;
    /* background-color: #D9D9D9 !important; */
}

.patientlist .MuiTableCell-body:last-child{
    text-align: center !important;
}
.patientlist .MuiTypography-h6{
    align-items: center;
    display: flex;
}
.patientlist .MuiButton-containedPrimary{
    min-width: fit-content !important;
    /* background-color: #747474 !important; */
    /* box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px rgba(0, 0, 0, 0.14), 0px 1px 5px rgba(0, 0, 0, 0.12) !important; */
    border-radius: 4px !important;
}
.patientlist .MuiButton-containedPrimary > a {
    text-decoration: none;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;    
    letter-spacing: 0.4px;
    text-transform: uppercase;    
    color: #FFFFFF;
    }
.patientlist .MuiPaper-root{
    /* border: 1px solid #D1D1D1; */
    /* border-radius: 8px !important; */
}
.patientlist .MuiPaper-root, .MuiCardContent-root{
    padding: 0px !important;
    margin: 0px !important;
}
#patientTableTitle {
    /* color: #2E2E2E; */
    /* font-family: 'Inter'; */
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
}
#patientTableTitle input{
    width: 300px !important;
    background-color: white;
    border-radius: 5px !important;
    border: 2px solid #A2A2A2 !important;
}
#patientTableTitle input:focus-visible{
    outline: none ;
}
.patientlist .MuiTableCell-body > h5, .patientlist .MuiTableCell-body > h6{
    /* color: #171717 !important; */
    font-style: normal !important;
    font-weight: 500 !important;
    font-size: 14px !important;
    line-height: 17px !important;
}
.patientlist .MuiTableCell-head> h5{
    /* color: #171717 !important; */
    font-style: normal !important;
    font-weight: 600 !important;
    font-size: 14px !important;
    line-height: 17px !important;
}

@media (min-width: 600px){
    .patientlist .MuiToolbar-root {
    padding-left: 16px;
    padding-right: 16px !important;
}
}


