.tabpanelContent{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 32px;
    gap: 20px; 
    background: white;
    border-radius: 4px; 
    flex: none;
    order: 1;
    align-self: stretch;
    flex-grow: 0;
    margin: 30px 0px;
    box-shadow: 0px 7px 30px 0px rgb(90 114 123 / 11%) !important;

}
.panelHeading{
    
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 20px !important;
    line-height: 24px;

    /* color: #737373; */

}
.tabpanelContent input, .tabpanelContent textarea{
    
    /* background-color: white; */
    border-radius: 5px !important;  
     border: 1px solid #A2A2A2 !important;
}
.tabpanelContent input#Referred_By{
    width: 400px;
}
.MuiInputBase-root.MuiOutlinedInput-root{
